.bg_img{
    width: 100%;
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0;
}
.profile_img{
    position: relative;
    background-size: cover;
    background-position: center;
    width: 20em;
    height: 17em;
}
.profile_img:hover .image_chooser{
    opacity: 1;
    transition: all .2s ease;
}
.profile_desc{
    padding: 1em
;
}
.name{
    font-size: 1.6em;
    color: #111111;
}
.image_chooser{
    position: absolute;
    bottom: .8em;
    left: 27%;
    opacity: 0;
    height: 1.4em;
    display: flex;
    align-items: center;
    width: 9.2em;
    overflow: hidden;
}
.image_chooser input {
    border: none;
}
.photo_icon{
    height: 100%;
    padding: 0 .2em;
}